.sign_page {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}

.body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  height: 100vh;
  margin: -20px 0 50px;
  
}
.hidden {
  display: flex;
}
h1 {
  font-weight: bold;
  margin: 0;
}
.Left_arrow {
  background-color: transparent;
  border-color: #ff4b2b;
}
.Left_arrow:hover {
  transform: translateX(10px);
  color: #000000;
}
.butt {
  display: flex;
  gap: 10px;
}
.butt p{
  color: black;
}
.right_arrow {
  padding: 0;
  border-style: none;
  color: rgb(186, 186, 186);
  margin-top: -10px;
  background-color: transparent;
  cursor: pointer;
}
.right_arrow:hover {
  transform: translateY(-2px);
  color: #ff416c;
  transform: rotateY(10deg);
}
h2 {
  text-align: center;
}

p {
  font-size: 12px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

/* span {
  font-size: 12px;
} */

a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

button {
  border-radius: 20px;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  margin-top: 5px;
  padding: 12px 32px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}
.ghost1_butt {
  position: fixed; 
  top: 20px;
  right: 20px;
  z-index: 999;
}
.ghost1 {
  border: 1px solid #e6e3e3;
  background-color: #ede9ea;
  color: #1a1919;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  padding: 8px 12px;
}
.ghost1:hover {
  border: 5px solid #dc7608;
  background-color: transparent;
  color: #7b7474;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  padding: 8px 12px;
}
button:active {
  transform: scale(0.95);
}

button:focus {
  outline: none;
}

button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}

form {
  
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.box {
  background-color: #eee;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
  border-radius: 10px;
border-color: #dddddd;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container.animate {
  transform: translateX(0%) scale(1);

  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    transform: translateX(100%) scale(0);
    z-index: 1;
  }

  50%,
  100% {
    transform: translateX(0%) scale(1);
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #ff416c;
  background: -webkit-linear-gradient(to right, #ff4b2b, #ff416c);
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
  font-size: 30px;
  color: white;
  background-color: #7b7474;
}
.social-container a:hover{
  color: rgb(238,174,202);
color: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
}
#sign_butt{
  color: black;
  margin-top: 30px;
}



